<template>
    <div itemscope itemtype="http://schema.org/ItemList" id="list_wrap">
        <div id="list_scroll">
            <ul>
                <list-object v-for="(val, idx) in alarmList"
                             :key="`alarmList${idx}`"
                             :listData="val"
                             :isLast="alarmList.length === (idx + 1)"
                             :index="idx + 1"></list-object>
            </ul>
            <p v-if="isLoaded" itemprop="description" id="list_desc">최근 14일 이내의 알림만 보관됩니다.</p>
            <loading-obj v-if="isLoading"></loading-obj>
        </div>
    </div>
</template>

<script>
    import ListObject from './ListObject.vue';
    import LoadingObj from '../LoadingObj.vue';

    export default {
        name: 'ListGroup',
        props: ['list'],
        data() {
            return {
                totalData: {},
                alarmList: [],
                next: '',
                limit: 10,
                isLoading: false,
                retryAccessCnt: 3,
                offset: 0,
                listWrapper: null,
                scrollTop: 0,
                flag: null,
                reqIdx: null
            }
        },
        created() {
            GEB.$on('initList', () => {
                this.$store.state.isErr = false;
                this.initList();
            });
            GEB.$on('saveBeforeSts', (listIdx) => {
                this.listWrapper.removeEventListener('scroll', this.scrollECallback);
                this.reqIdx = listIdx;
                this.scrollTop = this.listWrapper.scrollTop;
            });

            this.initList();
        },
        mounted() {
            const intervalEveryMinute = () => {
                GEB.$emit('renewPeriod');
            };
            clearInterval(intervalEveryMinute);
            setInterval(intervalEveryMinute, 60000);

            // TODO 리스트를 이전 스크롤 위치까지 로딩하고 스크롧바를 이동해야함
            webviewConn.$on('initializing', () => {
                // TODO 로딩바 제거 (삭제, 링크 시 로딩바 노출)
                let offset = this.limit;
                let listIdx = this.reqIdx;
                while ((offset / listIdx) < 1) {
                    offset += this.limit;
                }
                const reqUrl = `/api/v2/messages?limit=${offset}&offset=0`;
                this.initList(reqUrl, offset);
            });
        },
        computed: {
            isLoaded () {
                return this.$store.state.isLoaded;
            }
        },
        methods: {
            setAlarmList() {
                this.alarmList = this.alarmList.concat(this.totalData.results);
                if (this.alarmList.length === 0) {
                    this.$noti_ns.setPreloading(false);
                    this.$emit('emptyList');
                }

                return this.alarmList;
            },
            getList(usage, next, offset) {
                offset ? (this.flag = offset) : (this.flag = null);
                // TODO 테스트 api 서버 주소로 변경.
                if (next.indexOf('https://api2.glowpick.net') > -1) next = next.replace('https://api2.glowpick.net', '');
                this.$http.get(next)
                    .then(res => {
                        this.totalData = res.data;
                        this.setAlarmList();
                        /**
                         * GEB
                         * v-for 문으로 생성괴는 list-object.vue 중 가장 마지막 컴포넌트의 mounted 에서 쏘는 이벤트를 받는다 (height체크를 하기위해)
                         */
                        GEB.$on('domRendered', (objIdx) => {
                            if (!this.listWrapper) this.listWrapper = this.$el.childNodes[0];
                            if (objIdx !== this.alarmList.length) return false;
                            this.$noti_ns.setPreloading(false);
                            this.loadingManager(false);
                            this.$store.state.isLoaded = true;
                            if (this.next === this.totalData.next) return;
                            if (!this.totalData.next && !this.flag) return;
                            if (this.totalData.next) {
                                this.next = this.totalData.next;
                                if (this.flag) this.next = `/api/v2/messages?limit=${this.limit}&offset=${this.flag}`;
                            }
                            if (!this.next && !!this.listWrapper && !this.flag) {
                                this.listWrapper.removeEventListener('scroll', this.scrollECallback);
                                return;
                            }
                            if (this.flag) this.listWrapper.scrollTop = this.scrollTop;
                            this.checkHeight();
                        });
                    })
                    .catch(err => {
                        if (err.response.status === 401) {
                            this.retryForAccess();
                            return;
                        }
                        this.$noti_ns.setPreloading(false);
                        this.loadingManager(false);
                        this.$noti_ns.setErrorCallback(true, 'initList');
                    });
            },
            initList(reqUrl, offset) {
                this.$noti_ns.setPreloading(true);
                this.totalData = {};
                this.alarmList = [];
                this.next = '';
                let next = reqUrl || `/api/v2/messages?limit=${this.limit}&offset=0`;
                this.getList('init', next, offset);
            },
            moreList() {
                this.loadingManager(true);
                this.getList('more', this.next);
            },
            checkHeight() {
                if (!this.next) return;
                const listWrapper = this.listWrapper;
                if (listWrapper.scrollHeight === (listWrapper.scrollTop + listWrapper.clientHeight)) {
                    this.moreList();
                } else if (listWrapper.scrollHeight > (listWrapper.scrollTop + listWrapper.clientHeight)) {
                    listWrapper.addEventListener('scroll', this.scrollECallback);
                }
            },
            scrollECallback() {
                const listWrapper = this.listWrapper;
                if (listWrapper.scrollHeight === (listWrapper.scrollTop + listWrapper.clientHeight)) {
                    this.listWrapper.removeEventListener('scroll', this.scrollECallback);
                    this.moreList();
                }
            },
            loadingManager(boolean) {
                this.isLoading = boolean;
            },
            retryForAccess() {
                if (this.retryAccessCnt < 1) {
                    this.retryAccessCnt = 3;
                    this.$noti_ns.setErrorCallback(true, 'initList');
                    return;
                }
                this.retryAccessCnt--;
                webviewConn.$once('ntvGetAccessToken', () => {
                    this.initList();
                });
                webviewConn.reqAccessToken();
            }
        },
        components: {
            ListObject,
            LoadingObj
        }
    }
</script>

<style lang="scss">
    [itemtype$="ItemList"]#list_wrap {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding-top: 43px;
        #list_scroll {
            height: 100%;
            padding-bottom: 46px;
            overflow-y: scroll;
        }
    }
    [itemprop="description"]#list_desc {
        padding: 7px 0;
        line-height: 19px;
        text-align: center;
        font-size: 13px;
        font-weight: 500;
        color: #adacac;
    }
</style>



