import './polyfill.js';
import 'es6-promise/auto'

import Vue from 'vue';
import App from './App.vue';
import store from './store/store.js';
import router from './router';
import axios from 'axios';
import webviewConn from './webviewConn.js';
import notiGlobal from './noti_global.js';
import devCookie from './devCookie.js';

Vue.config.productionTip = false;
Vue.prototype.$http = axios;

window['GEB'] = new Vue();
window['webviewConn'] = webviewConn;

const NOTI = notiGlobal.init( store, router ) || {};

Vue.filter('parsingMsg', NOTI.parsingMsg);

const noti = (noti_ns => {
    const init = () => {
        intercept();
        getAppInfo();
    };

    const start = ()=>{
        mainDefault();
    };
    /**
     *
     * axios interceptor
     * @description 요청전 헤더를 구분해 전달한다
     */
    const intercept = () => {
        axios.interceptors.request.use( config=>{
            config.headers = store.getters.notiHeaders;
            if( config.method === 'get' ){
                config.headers['Expires'] = '-1';
                config.headers['Cache-Control'] = "no-cache,no-store,must-revalidate,max-age=-1,private";
            }
            return config;
        });

        webviewConn.init( store, noti_ns );
        webviewConn.$on('ntvOnBack', ()=>{NOTI.ntvBackGuard()});
        webviewConn.$on('ntvRoute', path=>{NOTI.ntvRoute(path)});
    };
    const getAppInfo = _ =>{
        let cookieTimer = 0;
        webviewConn.$on('ntvSetInfo', ( info ) => {
            store.state.useCookie = false;
            clearTimeout(cookieTimer);
            noti_ns.setAppInfo( info );
            start();
        });
        //
        webviewConn.getInfo();
        //
        cookieTimer = setTimeout( ()=>{
            if( store.state.useCookie ){
                noti_ns.setCookieInfo();
                start();
            }
        }, 1000 );
    };
    /**
     *  @description create vue instance
     *  @namespace noti_ns
     */
    const mainDefault = () => {
        Vue.prototype.$noti_ns = noti_ns;
        if( !store.state.isInit ){
            store.state.isInit = true;
            store.dispatch('ENV');
            new Vue({
                el: '#app',
                store,
                router,
                template: '<App/>',
                components: { App }
            });
        }
    };
    return {
        init
    }
})(NOTI);

devCookie(() => {NOTI.setCookieInfo()});

noti.init();


