<template>
    <div itemscope itemtype="http://schema.org/ItemList" id="top_wrap">
        <ul>
            <li itemprop="itemListElement" v-for="idx in 2" :class="{'first': idx === 1}">
                <btn-delete :id="`btn${idx}`"></btn-delete>
            </li>
        </ul>
    </div>
</template>

<script>
    import BtnDelete from './BtnDelete.vue';

    /**
     *
     */
    export default {
        name: 'topArea',
        components: {
            BtnDelete
        }
    }
</script>

<style lang="scss">
    [itemtype$="ItemList"]#top_wrap {
        z-index:100;position:relative;background:#fff;border-bottom:1px solid #ececec;height:43px;
        ul{
            overflow: hidden;padding:11px 0;
            [itemprop="itemListElement"] {
                display:inline-block;
                &.first {
                    padding-right:10px;border-right:solid 1px #d0d0d0;
                    button:after {content:"읽은 알림 삭제";}
                }
            }
        }
    }
</style>



