import Vue from 'vue';

export default (()=>{
    const _slice = Array.prototype.slice;
    let _store;
    /*// 디버깅 코드
    let _str = '';
    function trace(s){
        if(String(s).length > 0){
            _str = _str + `<p>${s}<p>`;
        }
        let layer = document.getElementById('layer');
        if(!!layer) layer.innerHTML = _str;
    }
    document.addEventListener('DOMContentLoaded', e=>{
        let dom = document.createElement('div');
        let styleObj = {
            display: 'block',
            position: 'absolute',
            width: '100%',
            height: '30%',
            color: '#FFFFFF',
            background: '#000',
            bottom: '0px',
            left: '0',
            'overflowY': 'scroll'
        };
        for ( let key in styleObj){
            dom.style.setProperty( key, styleObj[key] )
        }
        dom.id = 'layer'
        document.getElementById('app').appendChild(dom)
        trace('');
    });
    //*/
    return {
        _emitter: new Vue({}),
        _$emit: null,
        _$on: null,
        _inApp: false,
        _osType: '',
        _nextTime: -1,
        _startTime: Date.now(),
        init(store){
            _store = store;
            this._inApp = _store.getters.inApp;
            this._osType = _store.getters.osType;

            return this;
        },
        $emit(){
            this._emitter.$emit.apply( this._emitter, _slice.call( arguments, 0 ) );
        },
        $on(){
            this._emitter.$on.apply( this._emitter, _slice.call( arguments, 0 ) );
        },
        $once(){
            this._emitter.$once.apply( this._emitter, _slice.call( arguments, 0 ) );
        },
        $off(){
            this._emitter.$off.apply( this._emitter, _slice.call( arguments, 0 ) );
        },
        /**
         * @description info 를 받아 noti_global.setAppInfo 로 넘김.
         * @param info 앱, 디바이스, 회원정보 {string}
         */
        setinfo(info){
            if( info !== undefined){
                this._emitter.$emit('ntvSetInfo', info);
            }
        },
        /**
         * @description 앱에 getinfo 요청.
         * @returns {*|void}
         */
        getInfo(){
            let info = this.send( 'getinfo' );
            if( info !== undefined ) {
                this.setinfo( info );
                return info;
            }
        },
        /**
         * @description 앱에 데이터 저장
         * @param v {string}
         */
        setData(v=''){
            this.send( 'setdata', v );
        },
        /**
         * @description 앱에 저장된 데이터 요청
         * @return {*}
         */
        reqGetData(){
            let val = this.send( 'getdata' );
            if( val !== undefined ){
                this.getdata( val );
                return val;
            }
        },
        /**
         * @description 엡에 저장된 데이터 요청후 데이터 반환 받는 함수.
         * @param v
         */
        getdata(v){
            if( v !== undefined ){
                this._emitter.$emit('ntvGetData', v);
            }
        },
        /**
         * @description router push로 링크 이동
         * @param v
         */
        route(v){
            this._emitter.$emit('ntvRoute', v);
        },
        /**
         * receive type
         */
        onback(){
            this._emitter.$emit('ntvOnBack');
        },
        // TODO 함수이름은 앱과 얘기 후 바꾸기
        refreshing(){
            // TODO 리스트를 이전 스크롤 위치까지 로딩하고 스크롧바를 이동해야함
            this._emitter.$emit('initializing');
        },
        /**
         * @param v
         * @description api2 토큰수신
         */
        getaccesstoken(v){
            if( v !== undefined ){
                _store.commit( 'access', v );
                this._emitter.$emit('ntvGetAccessToken', v);
            }
        },
        /**
         * @description api2 토큰 요청
         */
        reqAccessToken(){
            let val = this.send( 'getaccesstoken' );
            if( val !== undefined ){
                this.getaccesstoken( val );
                return val;
            }
        },
        /**
         * @param v
         * @description 로그인 성공시 값 수신
         */
        login(v){
            if( v!==undefined ){
                _store.commit( 'idRegister', v );
                this._emitter.$emit('ntvLogin', v);
            }
        },
        /**
         * @param url 이동할 URL
         * @description 로그인 화면 요청
         */
        reqLogin( url = '' ){
            let val = this.send( 'login', url );
            if( val !== undefined ){
                this.login( val );
                return val;
            }
        },
        title( type, title='글로우픽스토어' ){
            this.send( 'title', `${title}||${type}` );
        },
        /**
         * @param isall {boolean} true : 모두닫기 및 앱 홈으로이동, false : 현재웹뷰닫기
         * @description 웹뷰닫기, 전체 또는 현재창
         */
        close( isall=false ){
            this.send( 'close', isall );
        },
        /**
         * @param url
         * @description 메인웹뷰(부모창) 링크 열기
         */
        parentOpen( url ){
            this.send( 'parentopen', url );
        },
        /**
         * 주문완료시 앱을 홈으로이동시키고, 웹뷰의 구매내역 열기
         */
        orderStatus(){
            this.send( 'orderstatus' );
        },
        /**
         * @param url
         * @description 새창 웹뷰열기
         */
        open( url ){
            this.send( 'open', url );
        },
        /**
         * @description 쇼핑백 카운트 갱신
         */
        bagCount(){
            this.send( 'bagcount' );
        },
        /**
         * @description 결제 완료 페이지에서 앱의 닫기 버튼의 플래그값 설정을 위한 함수 호출.
         */
        complete(){
            this.send( 'complete' );
        },
        /**
         * Google Analytics 기록을 위해 화면명을 앱으로 전송.
         * http://cf.glowdayz.com/pages/viewpage.action?pageId=12910968
         * @param name
         */
        ga(name){
            this.send( 'ga', name );
        },
        adbrix(name){
            this.send( 'adbrix', name );
        },
        /**
         * glowpickstore:// 프로토콜 사용.
         * @param type
         * @param code
         */
        send( type, code ){
            this._schemeCall( type, code );
        },
        /**
         * TODO :: 안드로이드 정의 필요.
         * glowpick://glowpick.com? 스키마 사용.
         * @param type
         * @param code
         * @return {*}
         */
        sendGlow(type, code){
            this._schemeCallGlow( type, code );
        },
        /**
         * @param uri
         */
        uriFrameCall( uri ){
            this._uriFrameCall( uri );
        },
        /**
         * 프레임 생성후 scheme 호출.
         * @param uri
         * @private
         */
        _uriFrameCall( uri ){
            let useLocation = false;
            let ver = parseFloat( _store.getters.deviceOs.replace( /^(\d+\.\d+).*$/, function($a, $1){
                return $1;
            }));

            if( _store.getters.osType === 'android' && ver < 4.4  ) {
                useLocation = true;
            }

            let now = Date.now();
            let gab = now - this._nextTime;
            let time = 0;
            let timeGab = 80;

            // 디버깅용 코드
            // let type = uri.replace( /^.+\?type=(.+)\&.+$/, ($a, $1)=>$1 );
            if( this._nextTime < 0 || gab >= timeGab ){
                time = 0;
                // trace(`${_store.getters.deviceOs} : ${type} || ${useLocation} ${now - this._startTime} `);
            }else{
                if( gab < timeGab && gab > 0 ){
                    time = timeGab-gab;
                }else if( gab < 0 ){
                    time = Math.abs(gab) + timeGab;
                }
            }
            this._nextTime = now + time;

            if( useLocation ){
                if(time > 0){
                    setTimeout(()=>{
                        document.location = uri;
                    }, time);
                }else{
                    document.location = uri;
                }
            }else{
                const parent = document.body;
                let frm = document.createElement('iframe');
                frm.id = '__iosfrm__';
                frm.width = 0;
                frm.height = 0;
                frm.setAttribute('style', 'height:0;width:0;border:0;border:none;visibility:hidden;');
                frm.src = uri;
                frm.onload = function frmOnload(){
                    parent.removeChild( frm );
                };
                parent.appendChild(frm);
                frm.remove();
            }

        },
        /**
         * TODO :: 미정의
         *
         */
        _androidGlowCall(){

        },
        /**
         *
         * @param type
         * @param code
         * @return {*}
         * @private
         */
        _androidCall( type, code ){
            if( arguments.length < 1 ){
                return;
            }
            let fn = arguments[0],
                params = Array.prototype.slice.call( arguments, 1 );
            if( fn === undefined || fn === null ){
                return;
            }

            try{
                // FIXME :  안드로이드 객체명 (window.glowpickstore) 임시로 넣음
                let native = window.glowpickstore;
                if( !!native ){
                    if( params.length > 0 ){
                        return native[fn].apply( native, params );
                    }else{
                        return native[fn].call( native );
                    }
                }else{
                }
            }catch(e){
                // FIXME : 디버깅 코드 제거필요
                console.log( 'native call exception :: ' + fn );
            }
        },
        /**
         *
         * @param type
         * @param code
         * @private
         */
        _schemeCall( type, code ){
            let uri = `glowpickstore://glowpick.com?type=${type}&code=${code}`;
            this._uriFrameCall( uri );
        },
        /**
         *
         * @param type
         * @param code
         * @private
         */
        _schemeCallGlow( type, code ){
            let uri = `glowpick://glowpick.com?type=${type}&code=${code}`;
            this._uriFrameCall( uri );
        }
    }
})();



