<template>
    <div>
        <h2 itemprop="alternativeHeadline">{{category || ''}}</h2>
        <p itemprop="description" v-html="$noti_ns.parsingMsg(this.msg)"></p>
        <date-obj itemprop="expires">{{getPeriod}}</date-obj>
    </div>
</template>

<script>
    import DateObj from './DateObj.vue';

    export default {
        name: 'listObjMsg',
        props: ['category', 'msg', 'createdDate'],
        data () {
            return {
                period: null
            }
        },
        mounted () {
            GEB.$on('renewPeriod', () => {
                this.setPeriod();
            });
        },
        computed: {
            getPeriod () {
                return this.period || this.setPeriod();
            }
        },
        methods: {
            setPeriod () {
                if (!this.createdDate) return '';
                const currDate = new Date();
                const createdDate = new Date(this.createdDate);
                const period = Math.floor((currDate.getTime() - createdDate.getTime()) / 1000 / 60);
                let computedPeriod;
                switch (true) {
                    case (period < 21600 && period >= 1440):
                        computedPeriod = Math.floor(currDate.getTime()/1000/60/60/24) - Math.floor(createdDate.getTime()/1000/60/60/24) + '일 전';
                        break;
                    case (period < 1440 && period >= 60):
                        computedPeriod = Math.floor(period/60) + 1 + '시간 전';
                        break;
                    case (period >= 1 && period < 60):
                        computedPeriod = period + '분 전';
                        break;
                    case (period < 1):
                        computedPeriod = '방금';
                        break;
                    default :
                        computedPeriod = '14일 이후';
                }
                this.period = computedPeriod;
                return this.period;
            }
        },
        components: {
            DateObj
        }
    }
</script>

<style lang="scss" scoped>
    [itemtype$="Message"] {
        color:#514e4e;font-size:14px;word-break:break-all;word-wrap:break-word;white-space:normal;
        [itemprop="alternativeHeadline"] {line-height:17px;font-weight:bold;}
        [itemprop="description"] {margin-top:4px;line-height:17px;font-weight:normal;letter-spacing:-0.1px;word-break:keep-all;}
        [itemprop="expires"] {display:block;margin-top:7px;line-height:13px;color:#adacac;font-size:11px;padding-bottom:4px;}
    }
</style>



