<template>
    <div id="basket">
        <empty-alarm v-if="isEmpty"></empty-alarm>
        <template v-else>
            <top-area v-if="isLoaded"></top-area>
            <list-group @emptyList="emptyList"></list-group>
        </template>
    </div>
</template>

<script>
    import EmptyAlarm from './EmptyAlarm.vue';
    import TopArea from './TopArea.vue';
    import ListGroup from './list/ListGroup.vue';

    export default {
        name : 'BodyWrap',
        data () {
            return {
                isEmpty: false,
                retryAccessCnt: 3,
                deletingPath: null
            }
        },
        created () {
            GEB.$on('deleting', (getPath) => {
                if (this.deletingPath !== getPath) return false;
                this.$store.state.isErr = false;
                this.deleting(getPath);
            });
            this.listenDeleteEvent();
        },
        computed: {
            isLoaded () {
                return this.$store.state.isLoaded
            }
        },
        methods: {
            deleting (path) {
                this.$http.delete(`/api/v2/messages/${path}`)
                    .then(res => {
                        // TODO 삭제하기전에 currPage, scrollTop 저장 후 로딩바 띄우기
                        GEB.$emit('saveBeforeSts');
                        webviewConn.$emit('initializing');
                    })
                    .catch(err => {
                        if (err.response.status === 401) {
                            this.retryAccess(path);
                            return;
                        }
                        this.$noti_ns.setErrorCallback(true, 'deleting', path);
                    });
            },
            /**
             * GEB
             * '읽은 알림 삭제', '모두삭제', 알람리스트의 x 버튼 클릭 시 동작
             *
             * @param 'read', 'all', ${alarmId} {string}
             */
            listenDeleteEvent () {
                GEB.$on('deleteList', (data) => {
                    this.deletingPath = data;
                    this.deleting(data);
                });
            },
            emptyList () {
                this.isEmpty = true;
            },
            retryAccess (parameter) {
                if (this.retryAccessCnt < 1) {
                    this.retryAccessCnt = 3;
                    this.$noti_ns.setErrorCallback(true, 'deleting', path);
                    return;
                }
                this.retryAccessCnt --;
                webviewConn.$once('ntvGetAccessToken', () => {
                    this.deleting(parameter);
                });
                webviewConn.reqAccessToken();
            }
        },
        components: {
            EmptyAlarm,
            TopArea,
            ListGroup
        }
    }
</script>

<style lang="scss" scoped>
    #basket {position:relative;height:100%;padding-bottom:30px;}
</style>



