<template>
    <figure itemprop="thumbnail" itemscope itemtype="http://schema.org/ImageObject">
        <img :src="img" itemprop="image" @error="defaultImg">
        <figcaption class="blind" itemprop="description">상품 설명</figcaption>
    </figure>
</template>

<script>
    export default {
        name: 'listObjPdt',
        props: ['img'],
        methods: {
            defaultImg () {
                arguments[0].target.src = `${this.$store.getters.imgContext.noti}/pdt_glowpick.png`;
            }
        }
    }
</script>

<style lang="scss" scoped>
    [itemtype$="ImageObject"] {
        width:45px;height:45px;
        [itemprop="image"] {display:block;width:100%;height:100%;}
    }
</style>



