<template>
    <button class="btn_delete_list" itemprop="potentialAction" @click="deleting(id)"><span class="icon_cancel"></span></button>
</template>

<script>
    export default {
        name: 'listObjDelete',
        props: ['id'],
        methods: {
            deleting (alarmId) {
                this.$noti_ns.setPreloading(true);
                GEB.$emit('deleteList', alarmId);
            }
        }
    }
</script>

<style lang="scss" scoped>
    [itemprop="potentialAction"] {
        display:block;cursor:pointer;width:33px;height:33px;text-align:center;
        span {display:inline-block;vertical-align:middle;}
    }
</style>



