<template>
    <div id="errWrap" itemscope itemtype="http://schema.org/WebSite">
        <figure itemprop="image">
            <img :src="`${$store.getters.imgContext.ec}/error.png`">
        </figure>
        <h1 itemprop="about">ERROR</h1>
        <h2 itemprop="about">일시적인 오류가 발생했습니다.</h2>
        <p itemprop="text">요청하신 페이지는 주소가 변경되었거나
            <br>
            삭제되었을 수 있습니다.</p>
        <button itemprop="url" @click="retrying">재시도</button>
    </div>
</template>

<script>
    export default {
        name: 'errWrap',
        created () {
            this.$noti_ns.setPreloading(false);
        },
        methods: {
            retrying () {
                const callbackData = this.$store.state.errCallback;
                GEB.$emit(callbackData.methodName, callbackData.parameter)
            }
        }
    }
</script>

<style lang="scss" scoped>
    #errWrap[itemtype$="WebSite"] {
        text-align:center;color:#63616d;
        [itemprop="image"] {
            margin:64px auto 0 auto;display:block;width:173px;height:146px;
            img {display:block;width:100%;height:100%;}
        }
        h1[itemprop="about"] {margin-top:20px;line-height:17px;font-size:19px;font-weight:900;}
        h2[itemprop="about"] {margin-top:6px;line-height:17px;font-size:15px;font-weight:bold;}
        [itemprop="text"] {margin-top:9px;line-height:14px;font-size:12px;font-weight:normal;color:#adacac;}
        [itemprop="url"] {margin-top:20px;display:inline-block;width:112px;height:40px;background-color:#514e4e;font-size:13px;font-weight:500;color:#fff;}
    }
</style>



