const state = {
    env: '',
    devImgCtx: {
        noti: 'https://d34nxr16ynwjcp.cloudfront.net/notification/dev',
        ec: 'https://d314mj44vu9dmc.cloudfront.net/dev/front-static'
    },
    prodImgCtx: {
        noti: 'https://d34nxr16ynwjcp.cloudfront.net/notification/prod',
        ec: 'https://d314mj44vu9dmc.cloudfront.net/prod/front-static'
    },
    imgCtx: {
        noti: 'https://d34nxr16ynwjcp.cloudfront.net/notification/prod',
        ec: 'https://d314mj44vu9dmc.cloudfront.net/prod/front-static'
    }
};
const getters = {
    env: state => {
        return state.env;
    },
    imgContext: state => {
        return state.imgCtx;
    }
};
const mutations = {
    ENV: (state, v) => {
        state.env = v;
    },
    IMG_CTX: (state, v) => {
        state.imgCtx = v;
    }
};
const actions = {
    ENV: ({commit, state}) => {
        let env = process.env;
        if (env.NODE_ENV === 'production') {
            if (env.DEBUG) {
                commit('ENV', 'staging');
                commit('IMG_CTX', state.devImgCtx);
            } else {
                commit('ENV', 'production');
                commit('IMG_CTX', state.prodImgCtx);
            }
        } else {
            commit('ENV', 'development');
            commit('IMG_CTX', state.devImgCtx);
        }
    }
};
export default {
    state,
    getters,
    mutations,
    actions
};



