import Vue from 'vue';
import Vuex from 'vuex';
import headers from './modules/headers.js';
import env from './modules/env.js';

Vue.use(Vuex);
/**
 *
 * @type {Store | Store<any>}
 */
const store = new Vuex.Store({
    state: {
        useCookie: true,
        isInit: false,
        isErr: false,
        used: true,
        preloading: false,
        isPreloadingVer: false,
        isLoaded: false
    },
    getters: {
        isPreloadingVer (state) {
            return state.isPreloadingVer;
        }
    },
    modules: {
        headers,
        env
    }
});

export default store;



