<template>
    <div itemscope itemtype="http://schema.org/WebPage">
        <figure itemprop="image">
            <span class="pictogram_list_empty"></span>
        </figure>
        <h1 itemprop="description">
            최근 14일 이내의 새로운 알림이 없습니다.
            <span>이벤트, 제품, 리뷰 등에 대한 알림을 받을 수 있어요.</span>
        </h1>
    </div>
</template>

<script>
    export default {
        name: 'emptyAlarm',
        created () {
            this.$noti_ns.setPreloading(false);
        }
    }
</script>

<style lang="scss" scoped>
    [itemtype$="WebPage"] {
        text-align:center;
        [itemprop="image"] {
            display:inline-block;margin-top:132px;width:157px;height:157px;
            span {display:inline-block;}
        }
        [itemprop="description"] {
            margin-top:20px;line-height:20px;text-align:center;color:#adacac;font-size:14px;font-weight:500;
            span {display:block;margin-top:2px;line-height:18px;font-size:12px;font-weight:normal;}
        }
    }
</style>



