<template>
    <button type="button" @click="deleting"><span class="icon_delete"></span></button>
</template>

<script>
    export default {
        name: 'btnDelete',
        props: ['id'],
        methods: {
            deleting () {
                this.$noti_ns.setPreloading(true);
                GEB.$emit('deleteList', (this.id === 'btn1' ? 'read' : 'all'));
            }
        }
    }
</script>

<style lang="scss" scoped>
    button {
        margin-left:14px;cursor:pointer;
        &:after {
            content:"모두삭제";
            display: inline-block;font-size: 12px;font-weight: 500;
            color: #363636;
        }
        span {display:inline-block;vertical-align:middle;margin-right:1px;}
    }
</style>



