export default (() => {
    let _store,
        _router;

    return {
        init(store, router) {
            _store = store;
            _router = router;
            return this;
        },
        replaceHref: str =>{
            return str.replace( /href=\"/gi, 'href=\"glowpick://glowpick.com?type=13&code=' );
        },
        /**
         * @param msg {string}
         */
        parsingMsg: msg=> {
            if (msg !== undefined) {
                return msg.replace( /\n/g, '<br>' );
            }
        },
        /**
         * @param type {String} 'Y' : 쇼핑백있을때 또는 메인 'N' : 쇼핑백 없을때
         * @param title {String} 타이틀명 단, 스토어 메인일경우 'main'
         * @description 앱의 상단 타이틀 및 상단 네비게이션 스타일 지정.
         */
        setTitle(type, title = 'glowpick') {
            if (!!document) document.title = title === 'main' ? 'glowpick' : title;
            webviewConn.title(type, title === 'main' ? '' : title);
        },
        ntvRoute(path){
            if(path){
                _router.push({path});
            }
        },
        ntvBackGuard() {
            let path = _router.history.current.path;
            switch (path) {
                case '/' :
                    webviewConn.close();
                    break;
                /*  TODO :: 웹뷰 분리로 주석처리, 테스트 후 삭제
                case '/payment' :
                    webviewConn.close();
                    break;*/
                case '/payment_status' :
                    webviewConn.close(true);
                    break;
                case '/order_status' :
                    webviewConn.close();
                    break;
                default :
                    this.back();
                    break;
            }
        },
        back(){
            if(window.history.length <= 1 || window.history.state === null){
                webviewConn.close();
            }else{
                _router.back();
            }
        },
        /**
         * @description info 를 받아 store에 저장 후 GEB 로 이벤트 발생.
         * @param info {string} 디바이스, 앱, 유저 데이터.
         */
        setAppInfo(info) {
            if( typeof info.split === 'function' ){
                const arr = info.split(',');
                let keyset, key;
                arr.forEach( val=>{
                    keyset = val.split(':');
                    key = keyset[0].replace(/[-_]/, '');
                    this.commitInfo(key, keyset[1], keyset[0]);
                });
            }
            GEB.$emit('initInfo');
        },
        setCookieInfo(){
            const info = document.cookie;
            if( typeof info.split === 'function' ){
                const arr = info.split('; ');
                let keyset, key;
                arr.forEach( val=>{
                    keyset = val.split('=');
                    key = keyset[0].replace(/[-_]/, '');
                    this.commitInfo( key, keyset[1], keyset[0] );
                } );
            }
        },
        commitInfo(key, val, delKey){
            const replacement = {
                'idregister': 'idRegister',
                'ostype': 'osType',
                'appversion': 'appVer',
                'uid': 'uid',
                'access': 'access',
                'devicemodel': 'deviceModel',
                'deviceos': 'deviceOs'
            };
            //
            if( replacement[key] !== undefined ){
                _store.commit(replacement[key], decodeURIComponent(val));
                if ( key !== 'ostype') {
                    document.cookie = delKey + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
                }
            }
        },
        getCookie(key){
            let exp = new RegExp( `(?:^|\\;\\s)(?:${key})=(.+?)(?:\\;\\s|$)`, 'g' );
            let rlt = exp.exec( document.cookie ) || [];
            return rlt[1];
        },
        /**
         * 라우터가 아닌 팝업형태의 에러 메세지 표시.
         * @description setError(boolean) boolean : 표시/미표시
         *
         * @param bool
         */
        setError(bool){
            _store.state.isErr = bool;
        },
        setPreloading(bool){
            if (_store.state.isPreloadingVer) _store.state.preloading = bool;
        },
        setErrorCallback(bool, methodName, parameter){
            _store.state.errCallback = {
                methodName,
                parameter
            };
            _store.state.isErr = bool;
        },
        chkVer: (verstr, release) => {
            if( release.length === 0 ) return false;
            let state = 0;
            let numbers = verstr.split('.', release.length).map( v=>parseInt(v, 10) );
            //
            state = numbers.reduce((used, compare, i)=>{
                if( used !== 0 || Number.isNaN(compare) || release[i] === undefined ) return used;
                switch( true ){
                    case compare < release[i] : return -1;
                    case compare > release[i] : return 1;
                    default : return 0;
                }
            }, state);
            return state > -1;
        }
    }
})();



