<template>
<div id="app">
    <err-wrap v-if="isErr"></err-wrap>
    <router-view v-show="!isErr"></router-view>
    <modal-layer v-if="notUsed"></modal-layer>
    <template v-if="isPreloadingVer">
        <preloading v-if="preloading"></preloading>
    </template>
</div>
</template>

<script>
import {mapGetters} from 'vuex';
import ErrWrap from './components/ErrWrap.vue';
import Preloading from './components/preloading.vue';
import ModalLayer from './components/etc/Modal_layer.vue';

export default {
    name: 'app',
    created () {
        this.chkVerForPreloading();
        this.$noti_ns.setTitle('N', '알림함');
        webviewConn.ga('user_notification');
        webviewConn.adbrix('user_notification');
    },
    methods: {
        chkVerForPreloading () {
            const os = this.$store.state.headers.osType.toLowerCase();
            const verArr = this.$store.state.headers.appVer.split('.');
            const iosVerArr = [1, 10, 0];
            const aosVerArr = [1, 14, 0];
            let returnBool = false;
            let currVarArr;
            if (os === 'ios') currVarArr = iosVerArr;
            else if (os === 'android') currVarArr = aosVerArr;
            else {
                returnBool = true;
                this.$store.state.preloading = returnBool,
                this.$store.state.isPreloadingVer = returnBool;
                return;
            }
            (() => {for (let idx = 0; idx < verArr.length; idx ++) {
                let num = Number(verArr[idx]);
                if (num > currVarArr[idx]) return returnBool = false;
                if (num < currVarArr[idx]) return returnBool = true;
            }})();
            if (returnBool) {
                this.$store.state.preloading = returnBool,
                this.$store.state.isPreloadingVer = returnBool;
            }
        },
    },
    computed: {
        ...mapGetters([
            'isPreloadingVer'
        ]),
        isErr () {
            return this.$store.state.isErr;
        },
        preloading () {
            return this.$store.state.preloading;
        },
        notUsed(){
            return !this.$store.state.used;
        }
    },
    components: {
        ErrWrap,
        Preloading,
        ModalLayer
    }
}
</script>

<style lang="scss">
    html,body,h1,h2,h3,h4,h5,h6,p,div,a,em,span,li,form,ul,ol,fieldset,legend,table,tr,td,th,thead,tbody,tfoot,section,article,header,footer,textarea,
    nav,menu,input,img,figure,caption,figcaption,dl,dt,dd,button,label{
        margin:0;padding:0;border:0;outline:none;box-sizing:border-box;font-family:sans-serif;color:inherit;font-weight:inherit;font-size:inherit;
    }
    html{height:100%;overflow-y:auto;-webkit-overflow-scrolling:touch;}
    body{background:#f2f2f2;color:#363636;overflow-x:hidden;height:100%;}
    img{font-size:0;line-height:0}
    li{list-style:none;}
    button{background-color:transparent;}
    table{border-collapse:collapse;border-spacing:0;table-layout:fixed;}
    a{text-decoration:none;cursor:pointer;}
    textarea{resize:none;}
    button,input,select,textarea{-webkit-appearance:none;box-shadow:none;cursor:pointer;border-radius:0;}
    #app{height:100%;}
    .blind{font-size:0;width:0;height:0;line-height:0;visibility:hidden;opacity:0;}
</style>



