import Vue from 'vue';
import Router from 'vue-router';
import BodyWrap from '../components/BodyWrap.vue';

Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: '/',
    routes: [
        {
            path: '/webview/messages',
            name: 'BodyWrap',
            meta: {title: '알림함', ga: 'user_noficifation', ad: 'user_notification'},
            component: BodyWrap
        }
    ]
});

router.beforeEach((to, from, next)=>{
    const vm = router.app;
    // 안드로이드 os 버전체크 :: EC는 android에서 체크함.
    if( vm.$store.getters.osType === 'android' && !vm.$noti_ns.chkVer( vm.$store.getters.deviceOs, [4,4]) ){
        vm.$store.state.used = false;
        return false;
    }
    next();
})

export default router;



