<template>
    <div class="modal_mask">
        <div class="preloading">
            <figure>
                <img src="https://d314mj44vu9dmc.cloudfront.net/dev/front-static/loading.gif">
                <figcaption>잠시만 기다려 주세요...</figcaption>
            </figure>
        </div>
    </div>
</template>

<script>
    /**
     *
     */
    export default {
        name: 'preloading'
    }
</script>

<style lang="scss" scoped>
    .modal_mask {
        position:fixed;z-index:3000;top:0;left:0;width:100%;height:100%;background-color:rgba(255, 255, 255, 0.5);transition:opacity .3s ease;
        .preloading {position:absolute;width:164px;height:116px;left:50%;top:50%;margin-left:-82px;margin-top:-58px;background:#fff;border:solid 1px #ececec;text-align:center;}
        figure {margin-top:8px;}
        img {width:70px;height:70px;}
        figcaption {line-height:19px;color:#63616d;font-size:15px;font-weight:bold;}
    }
</style>



