<template>
    <transition name="modal">
    <div class="modal-mask">
        <div class="modal-wrapper">
            <div class="modal-container">
                <div class="popup">
                    <header>
                        이용안내
                        <!--<button type="button" class="bg_sp btn_close" @click="onClick"></button>-->
                    </header>
                    <article >
                        <p class="txt_main center">사용하시는 기기가 안드로이드 4.3 이하 버전인<br>경우, 지원하지 않는 서비스입니다.
                            운영체제를<br>업데이트하신 후 이용해주시기 바랍니다.<p>
                        <p class="txt_warn center"></p>
                        <p class="txt_info center"> 자세한 내용은 1:1문의 또는 고객센터<br>
                            (02-538-3966)로 문의해주세요.</p>
                        <p class="txt_sub">※ 고객센터 운영 시간<br>
                            평일 오전11:00~오후 6:00 (주말/공휴일 제외)</p>
                        <div class="btn_area">
                            <button type="button" class="btn_main red" @click="onClick">확인</button>
                        </div>
                    </article>
                </div>
            </div>
        </div>
    </div>
    </transition>
</template>

<script>
export default {
    name : 'modalLayer',
    props : ['ly_name', 'msg'],
    methods : {
        onClick() {
            webviewConn.close();
        }
    }
}
</script>

<style lang="scss">
    .modal-mask {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: table;
        transition: opacity .3s ease;
    }

    .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
    }

    .modal-container {
        width: 300px;
        margin: 0px auto;
        background-color: #fff;
        border-radius: 2px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        transition: all .3s ease;
        font-family: Helvetica, Arial, sans-serif;
    }
    .modal-enter {
        opacity: 0;
    }

    .modal-leave-active {
        opacity: 0;
    }

    .modal-enter .modal-container,
    .modal-leave-active .modal-container {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }

    .popup .center {text-align: center;}
    .popup header{line-height:46px;background-color:#ececec;text-align:center;}
    .popup header::after{content:"";display:block;clear:both;}
    .popup .btn_close{float:right;position:relative;display:block;cursor:pointer;width:14px;height:15px;top:15px;right:14px;background-position:-89px -20px;}
    .popup article{background-color:#fff;padding:14px 26px 15px 26px;}
    .popup .txt_main{line-height:20px;color:#363636;font-size:14px;font-weight:normal;text-align:center;letter-spacing: -1px; word-spacing: -1px;}
    .popup .txt_warn{color:#d0021b;padding:10px 0; font-size:15px;}
    .popup .txt_sub{margin-top:8px;line-height:15px;color:#adacac;font-size:11px;font-weight:normal;}
    .popup .txt_info{letter-spacing: -1px; font-size: 15px;}
    .popup .btn_area{margin-top:12px;display:flex;justify-content:center;}
    .popup .btn_main{cursor:pointer;display:block;width:97px;height:40px;line-height:40px;text-align:center;color:#fff;font-size:14px;font-weight:normal;background-color:#514e4e;}
    .popup .btn_main.red{background-color:#d0021b;}
    .popup .btn_main:nth-child(2){margin-left:5px;}
    .bg_sp {
        background-repeat: no-repeat;
        //background-image: url(#{$CONTEXT}/front-static/sp_ec.png);
        background-size: 176px 250px;
    }
</style>



