<template>
    <li itemprop="itemListElement"
        :class="{'checked': isChecked}">
        <div itemprop="url" @click="checkingAlarm(listData.id)">
            <img-obj itemprop="image" class="main"
                     :img="listData.licon_image"
                     :isLike="listData.category"
                     :category="listData.category"></img-obj>
            <msg-obj itemscope itemtype="http://schema.org/Message"
                     :class="{'noRImg': !listData.ricon_image}"
                     :category="listData.category"
                     :msg="listData.message"
                     :createdDate="listData.created_at"></msg-obj>
            <pdt-obj itemprop="image" class="pdt"
                     v-if="listData.ricon_image"
                     :img="listData.ricon_image"></pdt-obj>
            <conts-obj v-if="listData.big_picture_image"
                       :url="listData.big_picture_image"></conts-obj>
        </div>
        <delete-obj itemprop="potentialAction"
                    :id="listData.id"></delete-obj>
    </li>
</template>

<script>
    import ImgObj from './ImgObj.vue';
    import MsgObj from './MsgObj.vue';
    import PdtObj from './PdtObj.vue';
    import DeleteObj from './DeleteObj.vue';
    import ContsObj from './ContsObj.vue';

    export default {
        name : 'ListObject',
        props: ['listData', 'isLast', 'index'],
        data () {
            return {
                isChecked: this.listData.is_checked,
                accessCnt: 3
            }
        },
        created () {
            GEB.$on('checkingAlarm', (getId) => {
                if (this.listData.id !== getId) return false;
                this.$store.state.isErr = false;
                this.checkingAlarm(getId);
            })
        },
        mounted () {
            if (this.isLast) {
                GEB.$emit('domRendered', this.index);
            }
        },
        methods: {
            checkingAlarm (listId) {
                this.$noti_ns.setPreloading(true);
                this.$http.put(`/api/v2/messages/${listId}/check`)
                    .then(() => {
                        this.$noti_ns.setPreloading(false);
                        // TODO 링크 이동하기전에 currPage, scrollTop 저장 후 로딩바 띄우기
                        GEB.$emit('saveBeforeSts', this.index);
                        if (!this.listData.target_url) {
                            webviewConn.$emit('initializing');
                            return;
                        }
                        webviewConn.uriFrameCall(this.listData.target_url);
                    })
                    .catch(err => {
                        if (err.response.status === 401) {
                            this.retryAccess(listId);
                            return;
                        }
                        this.$noti_ns.setErrorCallback(true, 'checkingAlarm', listId);
                    });
            },
            retryAccess (listId) {
                if (this.accessCnt < 1) {
                    this.accessCnt = 3;
                    this.$noti_ns.setErrorCallback(true, 'checkingAlarm', listId);
                    return;
                }
                this.accessCnt --;
                webviewConn.$once('ntvGetAccessToken', () => {
                    this.checkingAlarm(listId);
                });
                webviewConn.reqAccessToken();
            }
        },
        components: {
            ImgObj,
            MsgObj,
            PdtObj,
            DeleteObj,
            ContsObj
        }
    }
</script>

<style lang="scss" scoped>
    [itemprop="itemListElement"] {
        position:relative;
        &.checked [itemprop="url"] {background-color:#fff;}
        [itemprop="url"] {display:block;cursor:pointer;position:relative;min-height:92px;padding:16px 14px 15px 14px;background-color:#fff6f6;border-bottom:solid 1px #ececec;}
        [itemprop="image"].main {float:left;width:40px;height:40px;overflow:hidden;}
        [itemtype$="Message"] {
            overflow:hidden;padding:0 56px 0 20px;
            &.noRImg {padding:0 14px 0 20px;}
        }
        [itemprop="image"].pdt {position:absolute;top:37px;right:14px;}
        [itemprop="potentialAction"] {position:absolute;top:0;right:0;}
    }
</style>



