<template>
    <figure itemscope itemtype="http://schema.org/ImageObject">
        <img :src="img" :alt="category" itemprop="thumbnail" @error="defaultImg">
        <figcaption class="blind" itemprop="description">{{category}}</figcaption>
        <span class="badge_like" v-if="checkIsLike"></span>
    </figure>
</template>

<script>
    export default {
        name: 'listObjImg',
        props: ['img', 'isLike', 'category'],
        computed: {
            checkIsLike () {
                return this.isLike === '좋아요';
            }
        },
        methods: {
            defaultImg (event) {
                const defaultImg = `${this.$store.getters.imgContext.noti}/default_profile.jpg`;
                if (event.target.src === defaultImg) return event.target.src = '';
                else event.target.src = defaultImg;
            }
        }
    }
</script>

<style lang="scss" scoped>
    [itemtype$="ImageObject"] {
        position:relative;
        [itemprop="thumbnail"] {display:block;width:100%;height:100%;border-radius:50%;}
        span {position:absolute;display:block;right:0;bottom:0;}
    }
</style>



