const state = {
    idRegister: '',
    osType: '',
    uid: '',
    appVer: '',
    access: '',
    deviceModel: '',
    deviceOs: '',
    releaseVer: []
};
/**
 *
 * @type {{idRegister: (function(*)), osType: (function(*)), uid: (function(*)), appVer: (function(*)), access: (function(*)), inApp: (function(*)), isLogin: (function(*)), notiHeaders: (function())}}
 */
const getters = {
    idRegister(state){
        return state.idRegister;
    },
    osType(state){
        return state.osType;
    },
    deviceOs(state){
        return state.deviceOs;
    },
    uid(state){
        return state.uid;
    },
    appVer(state){
        return state.appVer;
    },
    releaseVer(state){
        return state.releaseVer;
    },
    access(state){
        return state.access;
    },
    inApp(state){
        let bool = state.osType.replace(/\s/g, '').length>0;
        return bool;
    },
    isLogin(state){
        return state.idRegister.replace(/\s/g, '').length > 0 && state.idRegister !== undefined && String(state.idRegister) !== '0';
    },
    notiHeaders(){
        return {
            Authorization: state.access,
            IDREGISTER: state.idRegister
        }
    }
};

const mutations = {
    idRegister(state, v){
        state.idRegister = v;
    },
    osType(state, v){
        state.osType = v;
        state.releaseVer = (()=>{
            switch( v ){
                case 'ios' : return [1, 9];
                case 'android' : return [1, 13];
                default : return [];
            }
        })();
    },
    uid(state, v){
        state.uid = v;
    },
    appVer(state, v){
        state.appVer = v;
    },
    access(state, v){
        state.access = v;
    },
    inApp(state, v){
        state.inApp = v;
    },
    deviceModel(state, v){
        state.deviceModel = v;
    },
    deviceOs(state, v){
        state.deviceOs = v;
    }
};

export default {
    state,
    getters,
    mutations
}



