export default (
    () => {
        if (!/ios|android/ig.test(window.navigator.userAgent) && ( process.env.NODE_ENV === 'development' || String(process.env.DEBUG) === 'true' )) {
            return (setCookieInfo) => {
                function setCookie(cName, cValue, cDay){
                    let expire = new Date();
                    expire.setDate(expire.getDate() + cDay);
                    let cookies = cName + '=' + escape(cValue) + '; path=/ '; // 한글 깨짐을 막기위해 escape(cValue)를 합니다.
                    if(typeof cDay != 'undefined') cookies += ';expires=' + expire.toGMTString() + ';';
                    document.cookie = cookies;
                }
                setCookie('access', 'Bearer zALJLqlwFt4uQ9jAczlHDw2dK1wKmw');
                setCookie('id-register', '858991');
                setCookieInfo();
            }
        } else return () => {}
    }
)();



